<template>
  <div>
    <h1 class="page-header">{{ title }}</h1>

    <div
      v-for="item in subjects"
      :key="item.id"
      class="media">
      <div class="media-left">
        <!-- path: '/movie/:id/:name'  设置:id 的值， 使用params: { id:1 } -->
        <router-link :to="{name: 'movie', params: { id: item.id }}">
          <img class="media-object" :src="'https://images.weserv.nl?url=' + item.images.small.substr(7)" alt="">
        </router-link>
      </div>
      <div class="media-body">
        <h2 class="media-heading">{{ item.title }}</h2>
        {{ item.genres }}
      </div>
    </div>
  </div>
</template>

<script>
import { mapState, mapActions } from 'vuex';
export default {
  created() {
    // console.log('created');
    // 获取当前的路由规则  path: "/coming_soon"
    // console.log(this.$route);
    // 执行actions
    // this.setData({
    //   path: this.$route.path
    // });
    this.setData();
  },
  methods: {
    ...mapActions('movieList', [
      'setData'
    ])
  },
  computed: {
    ...mapState('movieList', [
      'title', 'subjects'
    ])
  },
  watch: {
    '$route' (to, from) {
      // this.setData({
      //   path: to.path
      // });
      this.setData();
    }
  }
};
</script>

<style>

</style>



