import Vue from 'vue';
import Router from 'vue-router';
import movieList from '@/views/movie-list.vue';
import movieDetail from '@/views/movie-detail';
Vue.use(Router);

export default new Router({
  routes: [
    {
      name: 'home',
      path: '/',
      redirect: {
        name: 'in_theaters'
      }
    },
    {
      name: 'in_theaters',
      path: '/in_theaters',
      component: movieList
    },
    {
      name: 'coming_soon',
      path: '/coming_soon',
      component: movieList
    },
    {
      name: 'top250',
      path: '/top250',
      component: movieList
    },
    {
      name: 'search',
      path: '/search',
      component: movieList
    },
    {
      name: 'movie',
      path: '/movie/:id',
      component: movieDetail
    }
  ]
});



